/*style.scss*/
@import '../fonts';
@import '../_config';


/*
    style.css
    Wezom wTPL v4.0.0
*/
/*-------------------------------  Defs & Sys  ------------------------------------*/
    body {
        position: relative;
        min-width: 320px;
        font-size: $fontSize;
        line-height: $emSizeText;
        color: $colorBlack;
        background-color: $colorWhite;
        -webkit-font-smoothing: antialiased; 
    }
    body, button, input, select, textarea {
        font-family: $fontFamily;
    }
    a {
        color: $colorLink;
    }
    .w_tac { text-align: center; }
    .w_tar { text-align: right; }
    .w_tal { text-align: left; }
    .w_fll { float: left; }
    .w_flr { float: right; }
    .w_cls { font-size: 0; display: table; width: 100%;}
    .w_ovh { overflow: hidden; }
    .w_clear { clear: both;}
    .w_blank { height: 25px; }
    .w_first {margin-top: 0 !important;}
    .w_last {margin-bottom: 0 !important;}
    .w_clearfix, .wSize, .wTxt {
        &:after {
            content: '';
            display: block;
            clear: both;
            height: 0;
        }
    }
    .c_light {
        background: #f1f6f8;
    }
    .w_block {
        display: block;
        position: relative;
        width: auto;
        background-color: #f8f8f8;
        padding: 2% 3% 2.5%;
        border: 1px solid #ddd;
        border-radius: 3px;
        margin-bottom: 3.5%;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .svgHolder {
        position: relative;
        & > svg {
            @include HVcenter(100%, 100%);
        }
    }
/*-------------------------------  structure  ------------------------------------*/
    .wWrapper {
        min-height: 100%;
        height: auto !important;
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .wSize {
        max-width: 1240px;
        padding: 0 10px;
        margin: 0 auto;
        position: relative;
    }
    .padding_in {
        padding: 0 18px;
        box-sizing: border-box;
    }
    .wContainer {
        padding: 0 0 $footerSize 0;
        position: relative;
    }
    .wBlock {
        position: relative;
        padding: 40px 0 0;
        box-sizing: border-box;
    }
    .wFooter {
        position: relative;
        margin: -$footerSize auto 0;
        height: $footerSize;
    }
/*-------------------------------  wHeader  ------------------------------------*/
    
    .wHeader {
        position: relative;
        text-align: center;
    }

    /*-------------------------------  wHeader__top  ------------------------------------*/

        .wHeader__top {
            position: relative;
            background: $colorLight;
            padding: 10px 0 5px;
            border-bottom: 3px solid $colorMiddleLight;
        }

        .wHeader__logo {
            display: inline-block;
            vertical-align: top;
            float: left;
            width: 219px;
            max-height: 55px;
            padding-left: 10px;
        }

        .wHeader__logo > span,
        .wHeader__logo > a {
            display: block;         
        }

        .wHeader__logo img {
            vertical-align: top;
            width: 100%;
        }

        .wHeader__btn {
            float: right;
            margin: 5px 0 0 20px;
        }

        .wHeader__contacts {
            position: relative;
            display: inline-block;
            overflow: hidden;
            padding-left: 55px;
            margin-top: 5px;
            line-height: 0;
        }

        .wHeader__contacts .svgHolder {
            position: absolute;
            top: 0;
            left: 0;
            width: 23px;
            height: 39px;
        }

        .wHeader__contacts .svgHolder svg {
            fill: $colorBlue;
        }


        .wHeader__contacts > a {
            display: inline-block;
            vertical-align: top;
            font: 700 42px/32px 'OpenSans';
            color: $colorBlue;
            text-decoration: none;
            transition: color 0.2s linear;
            margin-bottom: 5px;
        }

        .wHeader__contacts > a:hover {
            color: $colorDarkBlue;
        }


        .wHeader__contacts > p {
            font: 300 16px/18px 'FuturaPT', sans-serif;
            color: $colorBlack;
        }


    /*-------------------------------  wHeader__bottom  ------------------------------------*/

        .wHeader__bottom {
            position: relative;
            background: $colorDark;
            transition: transform 0.3s linear;
        }

        .wHeader__bottom.p_fixed {
            position: fixed;
            top: 0;
            left: 50%;
            width: 100%;
            z-index: 100;
            transform: translateX(-50%);
        }

        .wHeader__menu ul {
            position: relative;
            display: table;
            width: 100%;    
            font-size: 0;
            line-height: 0;
            padding: 10px 0;
            -webkit-backface-visibility: hidden;
        }

        .wHeader__menu ul li {
            position: relative;
            display: table-cell;
            margin: 0 5px;
            padding: 0 15px;
        }

        .wHeader__menu ul li + li:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            width: 1px;
            height: 26px;
            background: #707c88;
            margin-top: -13px;
        }

        .wHeader__menu ul li a {
            position: relative;
            display: block;
            font: 400 16px/20px 'OpenSans';
            color: $colorWhite;
            padding: 10px 10%;
            text-decoration: none;
            border-radius: 8px;
            transition: color 0.2s linear, background 0.2s linear;
        }

        .wHeader__menu ul li a:hover {
            background: $colorWhite;
            color: $colorDarkBlue;
        }

        .wHeader__menu_btn {
            display: block;
            position: absolute;
            top: 6px;
            right: 10px;
            width: 34px;
            height: 28px;
            transform: rotate(0deg);
            transition: .5s ease-in-out;
            cursor: pointer;
            text-align: center;
            display: none;
        }

        .wHeader__menu_btn:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 100%;
            padding: 5px;
            background: $colorWhite;
            border: 1px solid #B4B4B4;
            transform: translate(-50%,-50%);
        }

        .js-anim_menu_btn {}

        .wHeader__menu_btn span {
            display: block;
            position: absolute;
            height: 6px;
            width: 50%;
            background: #1FB6FF;
            opacity: 1;
            z-index: 1;
            transform: rotate(0deg);
            transition: .25s ease-in-out;
        }

        .wHeader__menu_btn span:nth-child(even) {
            left: 50%;
            // border-radius: 0 6px 6px 0;
        }

        .wHeader__menu_btn span:nth-child(odd) {
            left:0px;
            // border-radius: 6px 0 0 6px;
        }

        .wHeader__menu_btn span:nth-child(1), 
        .wHeader__menu_btn span:nth-child(2) {
            top: 0px;
        }

        .wHeader__menu_btn span:nth-child(3), 
        .wHeader__menu_btn span:nth-child(4) {
            top: 10px;
        }

        .wHeader__menu_btn span:nth-child(5), 
        .wHeader__menu_btn span:nth-child(6) {
            top: 20px;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(1),
        html.mm-opened .wHeader__menu_btn span:nth-child(6) {
            transform: rotate(45deg);
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(2),
        html.mm-opened .wHeader__menu_btn span:nth-child(5) {
            transform: rotate(-45deg);
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(1) {
            left: 5px;
            top: 6px;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(2) {
            left: calc(50% - 4px);
            top: 6px;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(3) {
            left: -50%;
            opacity: 0;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(4) {
            left: 100%;
            opacity: 0;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(5) {
            left: 4px;
            top: 15px;
        }

        html.mm-opened .wHeader__menu_btn span:nth-child(6) {
            left: calc(50% - 4px);
            top: 15px;
        }


/*-------------------------------  wMenu  ------------------------------------*/
    .wMenu {
        position: relative;
        display: none;
    }

    .mm-menu {
        width: 100%;
        max-width: 305px;
    }


    .mm-listview {
        font-size: 0;
        line-height: 0;
    }

    .mm-listview > li {
        display: block;
        padding: 0;
        width: 100%;
        box-sizing: border-box;
    }

    .mm-listview > li a {
        padding: 10px;
        font: 700 italic 14px/20px 'Exo2';
        color: $colorWhite;
    }      

    .mm-menu.mm-theme-dark {
        background: #364657;        
    }

    .mm-menu .mm-listview {
        padding: 0 10px;
    }

    .mm-listview li {
        display: block;
        text-align: left;
    }

    .mm-listview li:after {
        left: 0 !important;
        border-color: #B4B4B4;
    }

    .mm-listview li.to_declare:after {
        border: none;
    }

    .mm-listview li a {
        display: block;
        font: 400 16px/20px 'OpenSans';
        color: #fff;
        padding: 10px 10%;
        // padding: 10px 20px !important;
        box-sizing: border-box;
        transition: color 0.2s linear, background 0.2s linear;
    }

    .mm-listview li a .mm-selected {
        padding: 10px 0;
    }

    .mm-listview > li > a {
        white-space: normal;
        text-overflow: none;
    }

    .mm-listview > li > a:not(.mm-next):hover {
        background: $colorWhite;
        color: #364657;
    }
    .mm-listview > li .mm-next:hover {
        background: rgba(255,255,255,.2);
    }

    .mm-listview > li .mm-next:hover ~ em.mm-counter {
        color: #364657;               
    }
    

    // .mm-listview .mm-next:hover {
    //     color: $colorDark;
    // } 

    .mm-listview li a:active {
        background: $colorBlack;
    }

    .mm-listview li.to_declare {
        text-align: center;
        margin-top: 25px;
    }

    .mm-listview li.to_declare button:hover {
        background: #E5E6E9;
        color: #364657;
    }

    .mm-listview li.to_declare button:active {
        background: $colorBlack;
        color: $colorWhite;
    }

    .mm-navbar-bottom .wFooter__social ul li a {
        border-color: $colorWhite;
    }

    .mm-navbar-bottom .wFooter__social ul li a:hover {
        background: $colorWhite;
    }


    .mm-navbar-bottom .wFooter__social ul li a svg {
        fill: $colorWhite;
    }

    .mm-navbar-bottom .wFooter__social ul li a:hover svg {
        fill: #757576;
    }

    html.mm-right.mm-opening .mm-slideout {
        transform: translate3d(-305px, 0, 0);
    }



