/*-------------------------------  wContainer  ------------------------------------*/
    /*-------------------------------  pageTitles  ------------------------------------*/
        .pageTitle{
            font-weight: 700;
            font-size: $fontSizeH1;
            line-height: $emSizeHeader;
            padding-bottom: $emSizeHalf;
            margin-bottom: $emSizePart;
        }
        .pageTitle.w_middle,
        .pageTitle.w_small {
            margin-top: $emSizePart;
            border-bottom-width: 0;

        }
        .pageTitle.w_middle {
            font-size: $fontSizeH1 * 0.7;
        }
        .pageTitle.w_small {
            font-size: $fontSizeH1 * 0.5;
        }
        .pageTitle.underlined{
            border-bottom: 1px solid $colorMiddleLight;
        }

        .wTitle {
            position: relative;
            font: 300 47px/56px 'OpenSans';
            color: $colorBlack;
            margin-bottom: 15px;
        }

        .c_white {
            color: $colorWhite;
        }


    /*-------------------------------  wLeft  ------------------------------------*/
        .wLeft {
            position: relative;
            float: left;
            width: 25%;
            margin-right: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wRight  ------------------------------------*/
        .wRight {
            position: relative;
            float: right;
            width: 25%;
            margin-left: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wMiddle  ------------------------------------*/
        .wLeft + .wMiddle,
        .wRight + .wMiddle {
            overflow: hidden;
        }
        .wMiddle {
            position: relative;
        }



    /*-------------------------------  wTop_block  ------------------------------------*/
        
        .wTop_block {
            position: relative;
            background: url(../pic/bg_top_block.jpg) center bottom no-repeat;
            // width: 1920px;
            height: 73px;
            line-height: 71px;
        }

    /*-------------------------------  wTop_block_menu  ------------------------------------*/

        .wTop_block_menu {
            position: relative;
            background: $colorBlue;
            height: 48px;
        }

        .wTop_block_menu ul {
            font-size: 0;
            line-height: 0;
            padding: 0 18px;
        }

        .wTop_block_menu ul li {
            display: inline-block;
            vertical-align: top;            
        }

        .wTop_block_menu ul li a {
            display: block;
            font: 15px/28px 'OpenSans';
            color: $colorWhite;
            text-decoration: underline;
            padding: 10px 16px;
            transition: background 0.2s linear, color 0.2s linear;
        }

        .wTop_block_menu ul li a:hover,
        .wTop_block_menu ul li a.curr {
            background: #f1f6f8;
            color: $colorBlue;
            text-decoration: none;
        }

        .wTop_block_menu ul li a.curr {
            text-decoration: underline;
            font-weight: 800;
            padding: 10px 20px;
            border-left: 1px solid $colorBlue;
            border-right: 1px solid $colorBlue;
        }

    /*-------------------------------  wTop_slider  ------------------------------------*/
    
        .wTop_slider_container {
            position: relative;
            max-width: 1920px;
            overflow: hidden;
            max-height: 773px;
            border-bottom: 3px solid #dadcdf;
            box-shadow: 0 0 40px 1px #000;
            z-index: 5;
            background: url(../pic/bg_top_slider.jpg) center top repeat;            
        }

        .wTop_slider {
            transform: translateZ(0); 
            position: relative;
            // background: url(../pic/bg_top_slider.jpg) center top no-repeat;
            height: 625px;
            width: 100%;
        }

        .wTop_slider__item {
            position: relative;
            height: 100%;  
            width: 100%;
            max-width: 1920px;
        }

        .wTop_slider__item__left {
            position: relative;
            // position: absolute;
            // top: 0;
            // left: 50%;
            height: 100%;
            width: 100%;
            max-width: 1240px;
            margin: 0 auto;
            // transform: translateX(-50%); 
            z-index: 10;        
        }

        .wTop_slider__item__left > div {
            position: absolute;
            top: 50%;
            left: 0;
            width: 420px;            
            transform: translateY(-50%);            
        }


        .wTop_slider__item--logo {
            position: relative;
            width: auto;
            margin-bottom: 30px;
        }

        .wTop_slider__item--title {
            position: relative;
            font: 60px/65px 'OpenSans';
            color: rgba(255,255,255,.65);
            margin-bottom: 30px;
        }

        .wTop_slider__item--title:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            background: url(data:image/png;base64,) repeat;
            // width: 409px;
            // height: 164px;
            width: 100%;
            height: 100%;
            overflow: hidden;
            opacity: .8;
        }


        .wTop_slider__item__img {
            position: absolute;
            top: 0;
            left: 50%;
            width: 1920px;
            margin-left: -960px;
        }

        .wTop_slider__item__img > div {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        .wTop_slider__item__img--front {
            z-index: 5;
        }
        .wTop_slider__item__img--front {
            transform: translateX(5%);
            transition: transform 1s 0.2s ease-out;
        }

        .wTop_slider__item__img--back {
            transform: translateX(-5%) scale(1.3);
            opacity: 0;
            transition: transform 1s 0.2s ease-out, opacity .6s 0.2s ease-out;
        }

        // .inviewRun .wTop_slider__item__img--back,
        .no_scale .wTop_slider__item__img--front,
        .no_scale .wTop_slider__item__img--back {
            transform: scale(1);
            opacity: 1;
        }



        .wTop_slider__bottom {
            position: relative;
            background: $colorBlue;
            height: 155px;
            line-height: 153px;
            overflow: hidden;
            border-bottom: 3px solid #dadcdf;
        }

        .wTop_slider__bottom--left {
            position: relative;
            overflow: hidden;
            // float: left;
            overflow: hidden;
            // width: 410px;
        }

        .wTop_slider__bottom--left > div {
            display: inline-block;
            vertical-align: middle;
        }

        .wTop_slider__bottom--left > div p {
            font: 700 42px/48px 'OpenSans';
            color: $colorWhite;
            text-transform: uppercase;
            margin-bottom: 10px;
        }

        .wTop_slider__bottom--left > div span {
            display: block;
            font: 300 25px/28px 'OpenSans';
            color: $colorWhite;
        }

        .wTop_slider__bottom--right {
            position: relative;
            float: right;
            width: 830px;
        }

        .wTop_slider__bottom--right ul {
            font-size: 0;
            line-height: 0;
            text-align: right;
            // margin-bottom: 35px;
        }

        .wTop_slider__bottom--right > ul li {
            display: inline-block;
            vertical-align: top;
            text-align: center;
            min-width: 200px;
            // padding-top: 15px;
            // margin: 0 35px;
            cursor: pointer;
        }

        .no-touch .wTop_slider__bottom--right > ul li {
            opacity: .2;
            transform: scale(0.5);            
        }

        .wTop_slider__bottom--right > ul li:nth-child(1) {
            transition: all 0.5s 0.5s linear;
        }

        .wTop_slider__bottom--right > ul li:nth-child(2) {
            transition: all 0.5s 1s linear;
        }

        .wTop_slider__bottom--right > ul li:nth-child(3) {
            transition: all 0.5s 1.5s linear;
        }

        .wTop_slider__bottom--right > ul li:nth-child(4) {
            transition: all 0.5s 2s linear;
        }


        .no-touch  .wTop_slider__bottom--right ul.inviewRun li {
            opacity: 1;
            transform: scale(1);
        }


        .wTop_slider__bottom--right > ul li > span {
            position: relative;
            display: block;
            padding: 15px 25px 5px; 
            z-index: 5;
        }

        .wTop_slider__bottom--right > ul li > span:before {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            // width: 100%;
            // height: 100%;
            border-bottom: 155px solid transparent;
            border-left: 55px solid transparent;
            border-right: 55px solid transparent;            
            height: 0;
            width: 140px;
            transition: border-color 0.3s linear;
            z-index: -1;
            transform: translateX(-50%);
        }
        
        .wTop_slider__bottom--right > ul li:nth-child(even) > span:before {              
            transform: translateX(-50%) rotate(180deg);
        }


        .wTop_slider__bottom--right > ul li > span:hover:before,
        .wTop_slider__bottom--right > ul li.active > span:before {
            // background: $colorWhite;
            border-bottom-color: $colorWhite;
        }

        .wTop_slider__bottom--right > ul li > span .svgHolder {
            position: relative;
            display: block;
            width: 84px;
            height: 84px;
            border-radius: 100%;
            border: 1px solid $colorWhite;
            box-sizing: border-box;
            background: transparent;
            margin: 0 auto 10px;
            transition: background 0.3s linear, border-color 0.3s linear; 
        }

        .wTop_slider__bottom--right > ul li > span:hover .svgHolder,
        .wTop_slider__bottom--right > ul li.active > span .svgHolder {
            background: $colorWhite;
            border-color: $colorBlue;
        }

        .wTop_slider__bottom--right > ul li > span .svgHolder svg {
            fill: $colorWhite;
            transition: fill 0.3s linear;
        }

        .wTop_slider__bottom--right > ul li > span:hover .svgHolder svg,
        .wTop_slider__bottom--right > ul li.active > span .svgHolder svg {
            fill: $colorBlue;
        }

        .wTop_slider__bottom--right > ul li > span .title {
            display: inline-block;
            vertical-align: top;
            font: 20px/22px 'OpenSans';
            color: $colorWhite;
            height: 44px;
            overflow: hidden;
            transition: color 0.3s linear;
        }

        .wTop_slider__bottom--right > ul li > span:hover .title,
        .wTop_slider__bottom--right > ul li.active > span .title {
            color: $colorBlue;
        }






    /*-------------------------------  wBlock.order_insurance_online  ------------------------------------*/
    
        .wBlock.order_insurance_online {
            background: url(../pic/bg_order_insurance_online.jpg) center top no-repeat;
            height: 958px;
            margin-top: -32px;
        }

        .no-touch .order_insurance_online.inviewI {
            opacity: .2;
            transform: translateY(20%);
            transition: opacity 1s linear, transform 0.75s 0.25s linear;            
        }

        .no-touch .order_insurance_online.inviewRun {
            opacity: 1;
            transform: translateY(0);
        }

        .wBlock.order_insurance_online:before {
            content: '';
            position: absolute;
            top: 40px;
            left: 50%;
            background: url(../pic/parashut_mans.png) center center no-repeat;
            width: 768px;
            height: 409px;
            margin-left: -384px;
        }

        .no-touch .wBlock.order_insurance_online:before {
            transform: translate(-50%, 25%);
            transition: transform 1.2s 0.75s ease-in-out;            
        }


        .no-touch .wBlock.order_insurance_online.inviewRun:before {
            transform: translate(0);            
        }

        .order_insurance_online__block {
            padding-top: 440px;
            max-width: 800px;
            margin: 0 auto;
            text-align: center;            
        }

        .order_insurance_online__block > span {
            display: block;
            font: 300 20px/24px 'FuturaPT';
            color: $colorBlack;
            height: 72px;
            margin-bottom: 10px;
        }

        .order_insurance_online__block > ul {
            font-size: 0;
            line-height: 0;
            margin-bottom: 35px;
        }

        .order_insurance_online__block > ul li {
            display: inline-block;
