        .wFooter__top__column {
            position: relative;
            display: inline-block;
            vertical-align: top;
            text-align: left;
            max-width: 290px;
            margin: 0 25px;
        }

        .wFooter__top__column > p {
            font: 20px/24px 'OpenSans';
            color: $colorBlack;
            margin-bottom: 15px;
            height: 24px;
            overflow: hidden;
        }

        .wFooter__top__column ul {
            position: relative;
            font-size: 0;
            line-height: 0;
        }

        .wFooter__top__column ul li {
            display: block;
            margin-bottom: 10px;
        }

        .wFooter__top__column ul li a {
            display: inline-block;
            vertical-align: top;
            font: 400 16px/20px 'FuturaPT';
            color: $colorBlue;
            text-decoration: none;
            transition: color 0.2s linear; 
        }

        .wFooter__top__column ul li a:hover {
            color: $colorDarkBlue;
            text-decoration: underline;
        }







